.product {
    -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
            box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
    width: 20%;
    margin: 1.2rem;
}

.product__text {
  padding: 0 1rem;
}


.prodcut__img--container {
    width: 100%;
    height: 10rem;
    overflow: hidden;
    margin: 0;
}

.prodcut__img {
    width: 100%;
    height: 100%;
    display: block;
    -webkit-filter: blur(5px);
            filter: blur(5px);
}


