.Form {
    display: -ms-flexbox;
    display: flex;
    background-color: rgb(184, 184, 184);
}


.form {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 3rem 0;
}


.form__preview {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 3rem 0;
}

.input {
    display: block;
    width: 80%;
    margin: 1rem 0;
    border: none;
    -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
            box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
    padding: .8rem .6rem;
    background-color: #fff;
}

.add__btn {
 padding: .8rem 1rem;
 border: none;
 border-radius: 4px;
 background-color: orange;
 color: #fff;
 -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
         box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
 -webkit-transition: all .6s;
 -o-transition: all .6s;
 transition: all .6s;   
 margin-top: 2.5rem;
}

.add__btn:hover {
    -webkit-box-shadow: 4px 4px 8px rgba(0, 0, 0, .3);
            box-shadow: 4px 4px 8px rgba(0, 0, 0, .3);
    cursor: pointer;
}


.preview__img {
    width: 15rem;
    height: 12rem;
}


